import * as React from "react";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertFromRaw, convertToRaw } from "draft-js";
import draftToHtml from "draftjs-to-html";
const styles = require("./index.less");
export default class LocalEditor extends React.Component {
    constructor(props) {
        super(props);
        this.onEditorStateChange = (editorState) => {
            const { onChange } = this.props;
            const raw = convertToRaw(editorState.getCurrentContent());
            const json = JSON.stringify(raw);
            const html = draftToHtml(raw);
            const plainText = editorState.getCurrentContent().getPlainText();
            onChange(json, html, plainText);
            this.setState({
                editorState
            });
        };
        const editorState = props.rawContent
            ? EditorState.createWithContent(convertFromRaw(JSON.parse(props.rawContent)))
            : EditorState.createEmpty();
        this.state = {
            editorState: editorState
        };
    }
    render() {
        const { editorState } = this.state;
        return (React.createElement("div", { className: styles.localEditor },
            React.createElement(Editor, { editorState: editorState, toolbarClassName: styles.editorToolbar, wrapperClassName: styles.editorWrapper, editorClassName: styles.editor, onEditorStateChange: this.onEditorStateChange, toolbar: { image: { uploadEnabled: true } } })));
    }
}



